
























































import Vue from "vue";
import vueSlider from "vue-slider-component";

import { throttle } from "lodash";
import { mapGetters, mapState } from "vuex";

import ConfirmDialog from "@/core/components/modals/confirm.vue";
import PromptDialog from "@/core/components/modals/prompt.vue";
import layerManager from "@/game/layers/manager";
import store from "@/game/store";
import InitiativeDialog from "@/game/ui/initiative.vue";
import MenuBar from "@/game/ui/menu/menu.vue";
import NoteDialog from "@/game/ui/note.vue";
import SelectionInfo from "@/game/ui/selection/selection_info.vue";
import Tools from "@/game/ui/tools/tools.vue";

import { createConnection } from "@/game/api/socket";
import { onKeyDown, onKeyUp } from "@/game/events/keyboard";
import { scrollZoom } from "@/game/events/mouse";
import { LocalPoint } from "@/game/geom";
import { l2g } from "@/game/units";

import "@/game/api/events";

export default Vue.component("game", {
    components: {
        "tool-bar": Tools,
        "selection-info": SelectionInfo,
        "prompt-dialog": PromptDialog,
        "confirm-dialog": ConfirmDialog,
        "menu-bar": MenuBar,
        "initiative-dialog": InitiativeDialog,
        "zoom-slider": vueSlider,
        "note-dialog": NoteDialog,
    },
    data: () => ({
        ready: {
            manager: false,
            tools: false,
        },
    }),
    computed: {
        ...mapState("game", ["IS_DM", "layers"]),
        ...mapGetters("game", ["selectedLayer"]),
        gridSize() {
            return this.$store.state.game.gridSize;
        },
        zoomFactor: {
            get(): number {
                return store.zoomFactor;
            },
            set(value: number) {
                store.updateZoom(value, l2g(new LocalPoint(window.innerWidth / 2, window.innerHeight / 2)));
            },
        },
    },
    beforeRouteEnter(to, from, next) {
        createConnection(to);
        next();
    },
    mounted() {
        // window.addEventListener("resize", () => {
        //     gameManager.layerManager.setWidth(window.innerWidth);
        //     gameManager.layerManager.setHeight(window.innerHeight);
        //     gameManager.layerManager.invalidate();
        // }),
        //     window.addEventListener("wheel", throttle(scrollZoom));
        // window.addEventListener("keyup", onKeyUp);
        // window.addEventListener("keydown", onKeyDown);
        // // prevent double clicking text selection
        // window.addEventListener("selectstart", e => {
        //     e.preventDefault();
        //     return false;
        // });
        this.ready.manager = true;
    },
    methods: {
        mousedown(event: MouseEvent) {
            (<any>this.$refs.tools).mousedown(event);
        },
        mouseup(event: MouseEvent) {
            (<any>this.$refs.tools).mouseup(event);
        },
        mousemove(event: MouseEvent) {
            (<any>this.$refs.tools).mousemove(event);
        },
        mouseleave(event: MouseEvent) {
            (<any>this.$refs.tools).mouseleave(event);
        },
        contextmenu(event: MouseEvent) {
            (<any>this.$refs.tools).contextmenu(event);
        },
        selectLayer(layer: string) {
            layerManager.selectLayer(layer);
        },
        drop(event: DragEvent) {
            if (event === null || event.dataTransfer === null) return;
            if (event.dataTransfer.files.length > 0) {
                (<any>this.$refs.confirm)
                    .open("Uploading files should be done through the asset manager.", "Ok", "")
                    .then(() => {}, () => {});
            } else if (event.dataTransfer.getData("text/plain") === "") {
                return;
            } else {
                layerManager.dropAsset(event);
            }
        },
    },
});
