




















import Vue from "vue";

import contextmenu from "@/core/components/contextmenu.vue";
import game from "@/game/game.vue";
import layerManager from "@/game/layers/manager";
import Settings from "@/game/settings";
import Shape from "@/game/shapes/shape";

import { l2gx, l2gy } from "@/game/units";

export default Vue.component("shape-menu", {
    components: {
        contextmenu,
    },
    data: () => ({
        visible: false,
        x: 0,
        y: 0,
        shape: <Shape | null>null,
    }),
    computed: {
        activeLayer: (): string => {
            const layer = layerManager.getLayer();
            return layer === undefined ? "" : layer.name;
        },
    },
    methods: {
        open(event: MouseEvent, shape: Shape) {
            this.visible = true;
            this.x = event.pageX;
            this.y = event.pageY;
            this.shape = shape;
            this.$nextTick(() => this.$children[0].$el.focus());
        },
        close() {
            this.visible = false;
            this.shape = null;
        },
        getLayers() {
            return layerManager.layers.filter(l => l.selectable && (this.$store.state.game.IS_DM || l.playerEditable));
        },
        getActiveLayer() {
            return layerManager.getLayer();
        },
        getInitiativeWord() {
            if (this.shape === null) return "";
            const initiative = (<any>game).$refs.initiative;
            return initiative.contains(this.shape.uuid) ? "Show" : "Add";
        },
        setLayer(newLayer: string) {
            if (this.shape === null) return;
            this.shape.moveLayer(newLayer, true);
            this.close();
        },
        moveToBack() {
            if (this.shape === null) return;
            const layer = this.getActiveLayer()!;
            layer.moveShapeOrder(this.shape, 0, true);
            this.close();
        },
        moveToFront() {
            if (this.shape === null) return;
            const layer = this.getActiveLayer()!;
            layer.moveShapeOrder(this.shape, layer.shapes.length - 1, true);
            this.close();
        },
        addInitiative() {
            if (this.shape === null) return;
            const initiative = (<any>game).$refs.initiative;
            if (!initiative.contains(this.shape.uuid)) initiative.addInitiative(this.shape.getInitiativeRepr());
            initiative.visible = true;
            this.close();
        },
    },
});
